.icon {
  width: 24px;
  height: 24px;
  display: block;
}

@media (width >= 576px) {
  .icon {
    width: 32px;
    height: 32px;
  }
}

.icon--phone {
  background-image: url("icon-phone.17246ea8.png");
}

@media (width >= 576px) {
  .icon--phone {
    background-image: url("icon-phone-32px.8ec49aa8.png");
  }
}

@media (width >= 1024px) {
  .icon--phone {
    transition: transform .3s;
    position: relative;
  }

  .icon--phone:hover {
    transform: scale(1.2);
  }

  .icon--phone:after {
    letter-spacing: 2px;
    color: #292929;
    content: "+1 234 555-55-55";
    opacity: 0;
    width: 140px;
    height: 32px;
    font-family: Inter, sans-serif;
    font-size: 12px;
    font-weight: 700;
    line-height: 32px;
    text-decoration: none;
    transition: all .3s;
    display: inline-block;
    transform: translateY(-16px);
  }

  .icon--phone:hover:after {
    transform-origin: 100%;
    opacity: 1;
    transform: translate(-140px, -16px);
  }
}

.icon--menu {
  background-image: url("icon-burger-menu.73ba25c4.png");
}

@media (width >= 1024px) {
  .icon--menu {
    transition: transform .3s;
  }

  .icon--menu:hover {
    background-image: url("icon-bur-menu-hover-desktop.a2721a3b.png");
    transform: scale(1.2);
  }
}

@media (width >= 576px) {
  .icon--menu {
