}

@media (width >= 576px) {
  .icon--close {
    background-image: url("icon-close-32px (1).b2da2c3e.png");
  }
}

@media (width >= 1024px) {
  .icon--close {
    transition: transform .3s;
  }

  .icon--close:hover {
    transform: scale(1.2);
